const FromControl = ({
  label,
  input,
  renderLabel,
  isRequired = false
}: {
  label: string | JSX.Element;
  input: JSX.Element;
  renderLabel?: () => JSX.Element;
  isRequired?: boolean
}) => {
  return (
    <div className="form-control">
      {renderLabel ? (
        renderLabel()
      ) : (
        <label className="form-label">{label} {isRequired ? <sup className="required" >*</sup>: null}</label>
      )}
      {input}
    </div>
  );
};

export default FromControl;
